(function () {
  var promiseChain = Promise.resolve();
  var callbacks = {};
  var init = function () {
    //   .
    // native callback  id callback .
    const guid = function () {
      function s4() {
        return Math.floor((1 + Math.random()) * 0x10000)
          .toString(16)
          .substring(1);
      }
      return (
        s4() +
        s4() +
        "-" +
        s4() +
        "-" +
        s4() +
        "-" +
        s4() +
        "-" +
        s4() +
        s4() +
        s4()
      );
    };

    /**
     * javascript => react-native
     * javascript react-native  .
     */
    window.webViewBridge = {
      send: function (targetFunc, data, success, error) {
        var msgObj = {
          targetFunc: targetFunc,
          data: data || {},
        };

        if (success || error) {
          msgObj.msgId = guid();
        }

        var msg = JSON.stringify(msgObj);

        promiseChain = promiseChain
          .then(function () {
            return new Promise(function (resolve) {
              console.log("react native   " + msgObj.targetFunc);

              if (msgObj.msgId) {
                callbacks[msgObj.msgId] = {
                  onsuccess: success,
                  onerror: error,
                };
              }

              window.ReactNativeWebView.postMessage(msg);
              resolve();
            });
          })
          .catch(function (e) {
            console.error("  " + e.message);
          });
      },
    };

    /**
     * react-native => javascript
     * react native   .
     * for android
     */
    document.addEventListener("message", function (e) {
      var message;
      try {
        message = JSON.parse(e.data);
        alert("call back event e.data ::" + e.data);
      } catch (err) {
        //console.error('   ' + err);
        return;
      }

      // callback .
      if (callbacks[message.msgId]) {
        if (message.isSuccessfull) {
          callbacks[message.msgId].onsuccess(message);
        } else {
          callbacks[message.msgId].onerror(message);
        }
        delete callbacks[message.msgId];
      }
    });

    /**
     * react-native => javascript
     * react native   .
     * for ios
     */
    window.addEventListener("message", function (e) {
      var message;
      try {
        message = JSON.parse(e.data);
        alert("call back event e.data ::" + e.data);
      } catch (err) {
        //console.error('   ' + err);
        return;
      }

      // callback .
      if (callbacks[message.msgId]) {
        if (message.isSuccessfull) {
          callbacks[message.msgId].onsuccess(message);
        } else {
          callbacks[message.msgId].onerror(message);
        }
        delete callbacks[message.msgId];
      }
    });
  };
  init();
})();
